.range__values {
  display: flex;
  border-radius: 2px;
  border: 1px solid #CBCBCB;
  font-size: 16px;
  margin-top: 30px;
}
.range__values:not(:last-child) {
  margin-bottom: 30px;
}
.range__values-input {
  padding: 12px 36px;
  width: 50%;
  color: var(--cod-gray);
  text-align: center;
  line-height: 1.25;
}
.range__slider {
  margin: 0 auto;
}